import React, { useEffect, useState } from "react";
import axiosInstance from "./axios";
import "./Comments.css";
import SingleComment from "./SingleComment";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";

function Comments({ productId }) {
  const [comments, setComments] = useState(null);
  const [newComment, setNewComment] = useState(null);
  const [loading, setLoading] = useState(false);

  async function getComments() {
    setLoading(true);
    const body = { product: productId };
    try {
      const response = await axiosInstance.get("storeApi/comments/", {
        params: { product: productId },
      });
      const result = await response;
      setComments(result.data);
      setLoading(false);
    } catch (error) {
      console.log(error);
    }
  }

  useEffect(() => {
    getComments();
  }, [productId]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const response = await axiosInstance.post("storeApi/comments/", {
      product: productId,
      comment: newComment,
    });
    const result = await response;
    if (result) {
      getComments();
      setNewComment("");
    }
  };

  if (loading) return <h1>Loading...</h1>;

  if (!comments)
    return <h2 className="section-title">no comments to display</h2>;

  return (
    <div className="comments">
      <div className="comments__block">
        {comments.map((comment) => {
          return <SingleComment {...comment} />;
        })}
      </div>
      <div className="comments__NewComment">
        <span>
          <AccountCircleIcon style={{ color: "lightgray", fontSize: 50 }} />
        </span>
        <form className="comment__form" onSubmit={handleSubmit}>
          <input
            type="text"
            name="newComment"
            value={newComment}
            onChange={(e) => setNewComment(e.target.value)}
          />
          <div className="button">
            <button type="submit">Add comment</button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Comments;
