import React, {
  createContext,
  useContext,
  useReducer,
  useEffect,
  useState,
} from "react";
import axiosInstance from "./axios";
import axios from "axios";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import reducer from "./reducer";

export const StateContext = createContext();

const initialState = {
  products: [],
  cart: [],
  cart_id: null,
  totalPrice: 0,
  totalItems: 0,
  orders: [],
  user: undefined,
  loading: false,
};

toast.configure();
export const StateProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const [suggestions, setSuggestions] = useState([]);
  const [text, setText] = useState({});

  const handleSearchChange = (e) => {
    const value = e.target.value;
    if (value.length === 0) {
      setSuggestions([]);
    } else {
      const regex = new RegExp(`^${value}`, "i");
      if (state.products) {
        const newSuggestions = [...state.products]
          .sort((a, b) => (a.title > b.title ? 1 : -1))
          .filter((v) => regex.test(v.title));
        console.log("suggestions", newSuggestions);
        setSuggestions(newSuggestions);
      }
    }
    setText(value);
  };

  const suggestionSelected = (value) => {
    setText(value);
    setSuggestions([]);
  };

  const getProducts = async () => {
    try {
      const response = await axios.get(
        `http://localhost:8000/storeApi/products/`
      );
      const result = await response;
      const newProducts = result.data;

      if (newProducts) {
        dispatch({
          type: "SET_PRODUCTS",
          payload: newProducts,
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getProducts();
  }, []);

  const addToCart = async (
    id,
    title,
    image,
    price,
    rating,
    description,
    category
  ) => {
    toast.success("Item added to the cart", {
      autoClose: 3000,
    });
    let body = { product: id, cart: state?.cart_id };
    const response = await axiosInstance.post(`storeApi/cart/`, {
      ...body,
    });
    const result = await response;
    if (result) {
      dispatch({
        type: "ADD_TO_CART",
        payload: { id, title, image, price, rating, description, category },
      });
    }
  };

  const removeFromCart = async (id) => {
    toast.error("Item removed from the cart", {
      autoClose: 3000,
    });
    let body = { product: id };
    console.log(body);
    const response = await axiosInstance.put(
      `storeApi/cart/${state.cart_id}/`,
      { data: body }
    );
    if (response) {
      dispatch({
        type: "REMOVE_FROM_CART",
        payload: { id },
      });
    }
  };

  const emptyCart = async () => {
    const response = await axiosInstance.delete(
      `storeApi/cart/${state.cart_id}/`
    );

    if (response) {
      dispatch({
        type: "EMPTY_CART",
      });
    }
  };

  const getCartTotal = (cart) =>
    cart?.reduce((amount, item) => item.price + amount, 0);

  const getOrders = async () => {
    const orderResponse = await axiosInstance.get(`storeApi/orders/`);
    const order = await orderResponse.data;
    if (order) {
      dispatch({
        type: "SAVE_ORDER",
        payload: { order },
      });
      return true;
    }
  };

  const saveOrder = async (payment_id, order) => {
    try {
      state.loading = true;
      let body = {
        product: order,
        payment: payment_id,
      };
      const responseOrder = await axiosInstance.post(`storeApi/orders/`, {
        ...body,
      });
      const result = await responseOrder.data;
      if (result) {
        const retriveOrders = await getOrders();

        if (retriveOrders) {
          return true;
        } else {
          return false;
        }
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getOrders();
  }, [state.user]);

  const saveUser = async () => {
    try {
      const responseUser = await axiosInstance.get(`dj-rest-auth/user/`);
      const user = await responseUser.data;
      // console.log(user);
      const responseCart = await axiosInstance.get(`storeApi/cart/`);
      // console.log(responseCart);
      const cart = await responseCart.data[0].product;
      const cart_id = await responseCart.data[0].id;
      // console.log(cart, cart_id);
      if (user) {
        // the user is logged in
        dispatch({
          type: "SET_USER",
          payload: { user, cart, cart_id },
        });
      } else {
        // the user is logged out
        user = null;
        cart = [];
        cart_id = null;
        dispatch({
          type: "SET_USER",
          payload: { user, cart, cart_id },
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    saveUser();
  }, []);

  const logoutUser = () => {
    const response = axios.post("http://localhost:8000/blacklist/", {
      refresh_token: localStorage.getItem("refresh_token"),
    });
    localStorage.removeItem("access_token");
    localStorage.removeItem("refresh_token");
    axiosInstance.defaults.headers["Authorization"] = null;
    // the user is logged out
    const user = null;
    const cart = [];
    const cart_id = null;
    dispatch({
      type: "SET_USER",
      payload: { user, cart, cart_id },
    });
  };

  return (
    <StateContext.Provider
      value={{
        ...state,
        addToCart,
        removeFromCart,
        emptyCart,
        getCartTotal,
        saveUser,
        saveOrder,
        logoutUser,
        getOrders,
        getProducts,
        handleSearchChange,
        suggestionSelected,
        text,
        suggestions,
      }}
    >
      {children}
    </StateContext.Provider>
  );
};

export const useStateValue = () => {
  return useContext(StateContext);
};
