import axiosInstance from "./axios";

const reducer = (state, action) => {
  switch (action.type) {
    case "SET_PRODUCTS":
      let newProducts = action.payload;
      return { ...state, products: newProducts };

    case "ADD_TO_CART":
      let newCart0 = [...state?.cart, action.payload];
      return { ...state, cart: newCart0 };

    case "GET_TOTAL":
      if (state?.cart) {
        let { totalPrice, totalItems } = state?.cart?.reduce(
          (cartTotal, cartItem) => {
            const { price } = cartItem;
            const itemTotal = price * 1;

            cartTotal.totalPrice += itemTotal;
            cartTotal.totalItems += 1;

            return cartTotal;
          },
          {
            totalPrice: 0,
            totalItems: 0,
          }
        );
        totalPrice = parseFloat(totalPrice.toFixed(2));

        return { ...state, totalPrice, totalItems };
      }

    case "REMOVE_FROM_CART":
      // get the index of the item to be removed
      const removeIndex = state?.cart.findIndex(
        (item) => item.id === action.payload.id
      );

      let newCart1 = [...state?.cart];

      // if we got the removeIndex
      if (removeIndex >= 0) {
        // remove single instace of the element from that index
        newCart1.splice(removeIndex, 1);
      } else {
        console.warn(
          `Cant remove product (id: ${action.id}) as its not in basket!`
        );
      }

      return { ...state, cart: newCart1 };

    case "SET_USER":
      // console.log(action.payload.cart_id);
      return {
        ...state,
        user: action.payload.user,
        cart: action.payload.cart,
        cart_id: action.payload.cart_id,
      };

    case "EMPTY_CART":
      let newCart2 = [];
      return { ...state, cart: newCart2 };

    case "SAVE_ORDER":
      // console.log(action.payload.order);
      return { ...state, orders: action.payload.order, loading: false };
  }
};

export default reducer;
