import React from "react";
import "./CheckoutProduct.css";
import { useStateValue } from "./StateProvider";
import { useHistory } from "react-router-dom";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import StarIcon from "@material-ui/icons/Star";

function CheckoutProduct({ id, image, title, price, rating, hideCartButton }) {
  const { removeFromCart } = useStateValue();
  const history = useHistory();

  const handleClick = () => {
    history.push(`/productDetail/${id}`);
  };

  return (
    <div className="checkoutProduct">
      <img src={image} className="checkoutProduct__image" />
      <div className="checkoutProduct__info">
        <p className="checkoutProduct__title">{title}</p>
        <p className="checkoutProduct__price">
          <small>$</small>
          <strong>{price}</strong>
        </p>
        <div className="checkoutProduct__rating">
          {rating &&
            Array(rating)
              .fill()
              .map((_, i) => {
                return (
                  <p>
                    <StarIcon style={{ color: "#ffb900", fontSize: "20" }} />
                  </p>
                );
              })}
          {rating &&
            Array(5 - rating)
              .fill()
              .map((_, i) => {
                return (
                  <p>
                    <StarBorderIcon
                      style={{ color: "#ffb900", fontSize: "20" }}
                    />
                  </p>
                );
              })}
        </div>
        <div className="checkouProdut__buttons">
          <button className="checkoutProduct__button1" onClick={handleClick}>
            Go To Product Page
          </button>
          {!hideCartButton && (
            <button
              className="checkoutProduct__button2"
              onClick={() => removeFromCart(id)}
            >
              Remove From Cart
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

export default CheckoutProduct;
