import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import axiosInstance from "./axios";
import StarIcon from "@material-ui/icons/Star";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import "./ProductDetail.css";
import Comments from "./Comments";
import { useStateValue } from "./StateProvider";
import SearchBar from "./SearchBar";

function ProductDetail() {
  const { id } = useParams();
  const { addToCart } = useStateValue();
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    async function getProduct() {
      try {
        const response = await axiosInstance.get(`storeApi/products/${id}/`);
        const result = await response;
        const data = result.data;
        console.log(data);
        if (data) {
          const { title, image, description, rating, price, category } = data;
          const newProduct = {
            title,
            image,
            description,
            rating,
            price,
            category,
          };
          console.log(newProduct);
          setProduct(newProduct);
          setLoading(false);
        }
      } catch (error) {
        console.log(error);
      }
    }

    getProduct();
  }, [id]);

  if (loading) return <h1>Loading...</h1>;

  if (!product) return <h2 className="section-title">no product to display</h2>;

  const { title, image, description, rating, price, category } = product;
  return (
    <div className="product__root">
      <div className="product__">
        <div className="product__details">
          <div className="product__details__image">
            <img src={image} alt={title} />
          </div>
          <div className="product__details__info">
            <div className="product__details__title">
              <h1>
                <strong>{title}</strong>
              </h1>
            </div>
            <div className="product__details__rating">
              {Array(rating)
                .fill()
                .map((_, i) => {
                  return (
                    <p>
                      <StarIcon style={{ color: "#ffb900", fontSize: "23" }} />
                    </p>
                  );
                })}
              {rating &&
                Array(5 - rating)
                  .fill()
                  .map((_, i) => {
                    return (
                      <p>
                        <StarBorderIcon
                          style={{ color: "#ffb900", fontSize: "23" }}
                        />
                      </p>
                    );
                  })}
              (105 customer reviews)
            </div>
            <div className="product__details__price">
              <h3>
                <strong>${price}</strong>
              </h3>
            </div>
            <br />
            <div className="product__details__description">
              <p>{description}</p>
            </div>
            <div className="product__details__additionalDetails">
              <p>
                <strong>Available : </strong>
                <div>In stock</div>
              </p>
              <p>
                <strong>SKU : </strong>
                <div>RecQ0fMd8T0Vk211E</div>
              </p>
              <p>
                <strong>Category : </strong>
                <div>{category}</div>
              </p>
              <br />
              <hr />
            </div>
            <br />
            <div className="product__details__button">
              <button
                onClick={() =>
                  addToCart(
                    id,
                    title,
                    image,
                    price,
                    rating,
                    description,
                    category
                  )
                }
              >
                Add to cart
              </button>
            </div>
          </div>
        </div>
        <div className="product__commentsRoot">
          <div className="comment__title">
            <p>Comments</p>
          </div>
          <div className="product__comments">
            <Comments productId={id} />
          </div>
        </div>
      </div>
      <div className="search__bar">
        <SearchBar />
      </div>
    </div>
  );
}

export default ProductDetail;
