import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import axios from "axios";
import React, { useState, useEffect } from "react";
import CurrencyFormat from "react-currency-format";
import { Link, useHistory } from "react-router-dom";
import CheckoutProduct from "./CheckoutProduct";
import "./Payment.css";
import { useStateValue } from "./StateProvider";
import SearchBar from "./SearchBar";

function Payment() {
  const { user, cart, loading, emptyCart, getCartTotal, saveOrder } =
    useStateValue();

  const stripe = useStripe();
  const elements = useElements();

  const [succeeded, setSucceeded] = useState(false);
  const [processing, setProcessing] = useState("");
  const [error, setError] = useState(null);
  const [disabled, setDisabled] = useState(true);
  const [clientSecret, setClientSecret] = useState(true);

  const history = useHistory();

  if (user === null) {
    history.push("/login");
  }

  useEffect(() => {
    // generate the special stripe secret
    // which allows us to charge a coustomer
    const getClientSecret = async () => {
      try {
        const response = await axios({
          method: "post",

          // Stripe excepts the total in a currencies submit
          // so convert dollars to cents
          url: `http://localhost:8000/payment/`,
          data: getCartTotal(cart) * 100,
        });
        if (response) {
          setClientSecret(response.data.clientSecret);
        }
        // else{
        //   console.log(undefined)
        // }
      } catch (error) {
        console.log(error);
      }
    };

    getClientSecret();
  }, [cart]);

  console.log("the secret key is", clientSecret);

  const handleSubmit = async (e) => {
    // some stripe stuff
    e.preventDefault();
    setProcessing(true);

    const payload = await stripe.confirmCardPayment(clientSecret, {
      payment_method: {
        type: "card",
        card: elements.getElement(CardElement),
      },
    });
    if (payload.error) {
      setError(`Payment failed ${payload.error.message}`);
      setProcessing(false);
    } else {
      setError(null);
      setProcessing(false);
      setSucceeded(true);
      saveOrder(clientSecret.split("_secret_")[0], { ...cart });
      emptyCart();
      history.replace("/orders");
    }
  };

  const handleChange = (e) => {
    // Listen for changes in the card element
    // and display any errors as the coustomer
    // types their card details
    setDisabled(e.empty);
    setError(e.error ? e.error.message : "");
  };

  const cardStyle = {
    style: {
      base: {
        color: "#32325d",
        fontFamily: "Arial, sans-serif",
        fontSmoothing: "antialiased",
        fontSize: "16px",
        "::placeholder": {
          color: "#32325d",
        },
      },
      invalid: {
        color: "#fa755a",
        iconColor: "#fa755a",
      },
    },
  };

  if (loading) {
    return <h1>Loading...</h1>;
  }

  return (
    <div className="payment__root">
      <div className="payment">
        <div className="payment__container">
          <h1>
            Checkout (<Link to="/checkout">{cart?.length} items</Link>)
          </h1>

          {/* Payment section - delivery address */}
          <div className="payment__section">
            <div className="payment__title">
              <h3>Delivery Address</h3>
            </div>
            <div className="payment__address">
              <p>{user?.email}</p>
              <p>123 React Lane</p>
              <p>Los Angeles, CA</p>
            </div>
          </div>

          {/* Payment section - Review Items */}
          <div className="payment__section">
            <div className="payment__title">
              <h3>Review items and delivery</h3>
            </div>
            <div className="payment__items">
              {cart?.map((item) => (
                <CheckoutProduct
                  id={item.id}
                  title={item.title}
                  image={item.image}
                  price={item.price}
                  rating={item.rating}
                />
              ))}
            </div>
          </div>

          {/* Payment section - Payment method */}
          <div className="payment__section">
            <div className="payment__title">
              <h3>Payment Method</h3>
            </div>
            <div className="payment__details">
              {/* Stripe magic will go */}

              <form onSubmit={handleSubmit}>
                <CardElement options={cardStyle} onChange={handleChange} />

                <div className="payment__priceContainer">
                  <CurrencyFormat
                    renderText={(value) => <h3>Order Total: {value}</h3>}
                    decimalScale={2}
                    value={getCartTotal(cart)}
                    displayType={"text"}
                    thousandSeparator={true}
                    prefix={"$"}
                  />
                  <button disabled={processing || disabled || succeeded}>
                    <span>{processing ? <p>Processing</p> : "Buy Now"}</span>
                  </button>
                </div>

                {/* Errors */}
                {error && <div>{error}</div>}
              </form>
            </div>
          </div>
        </div>
      </div>
      <div className="search__bar">
        <SearchBar />
      </div>
    </div>
  );
}

export default Payment;
