import React from "react";
import "./SingleComment.css";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";

function SingleComment({ id, comment, commenter }) {
  // console.log(comment, commenter);
  return (
    <>
      <div className="single__comment">
        <p>
          <span>
            <AccountCircleIcon style={{ color: "lightgray", fontSize: 50 }} />
          </span>
          <div>
            <p>
              <strong>
                {commenter.username.charAt(0).toUpperCase() +
                  commenter.username.slice(1)}
              </strong>
            </p>
            <p>{comment}</p>
          </div>
        </p>
      </div>
      <hr />
    </>
  );
}

export default SingleComment;
