import React, { useState } from "react";
import axiosInstance from "./axios";
import { Link, useHistory } from "react-router-dom";
import "./Login.css";
import { useStateValue } from "./StateProvider";
import ReportProblemOutlinedIcon from "@material-ui/icons/ReportProblemOutlined";

function Login() {
  // for redirecting to a page after successful login
  const history = useHistory();

  const { saveUser, logoutUser } = useStateValue();
  const [error, setError] = useState(false);

  const [user, setUser] = useState({
    email: "",
    password: "",
  });

  // const registerUrl = "http://localhost:8000/dj-rest-auth/registration/";

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;

    setUser({ ...user, [name]: value });
  };

  const loginUser = async () => {
    try {
      const response = await axiosInstance.post(`dj-rest-auth/login/`, {
        ...user,
      });
      const res = await response;
      if (res) {
        // console.log(res);
        localStorage.setItem("access_token", res.data.access_token);
        localStorage.setItem("refresh_token", res.data.refresh_token);
        axiosInstance.defaults.headers["Authorization"] =
          "JWT " + localStorage.getItem("access_token");
        saveUser();
        setError(false);
        history.push("/");
      }
    } catch (error) {
      if (error.response.status === 400) {
        setError(true);
      }
      logoutUser();
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    loginUser();
  };

  return (
    <div className="login">
      <Link to="/">
        <img
          className="login__logo"
          src="data:image/png;base64,"
          alt="amazon logo"
        />
      </Link>
      {error && (
        <div className="error__container">
          <ReportProblemOutlinedIcon style={{ color: "red", fontSize: 40 }} />
          <div className="error__label">
            <h3 color="red">There was a problem</h3>
            <h5>Invalid login id or password</h5>
          </div>
        </div>
      )}
      <br />
      <div className="login__container">
        <h1>Sign-In</h1>
        <form onSubmit={handleSubmit}>
          <h5>E-mail</h5>
          <input
            type="text"
            name="email"
            value={user.email}
            onChange={handleChange}
          />

          <h5>Password</h5>
          <input
            type="password"
            name="password"
            value={user.password}
            onChange={handleChange}
          />

          <button type="submit" className="login__signInButton">
            Sign In
          </button>
        </form>
        <p>
          By continuing, you agree to AMAZON FAKE CLONE Conditions of Use and
          Privacy Notice.
        </p>
        <Link to="/register">
          <button className="login__registerButton">
            Create your Amazon account
          </button>
        </Link>
      </div>
    </div>
  );
}

export default Login;
